import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faHome,
  faInfoCircle,
  faUser,
  faCog,
  faServer,
  faCloud,
  faBars,
  faTimes,
} from "@fortawesome/free-solid-svg-icons";

interface NavItem {
  name: string;
  href: string;
}

interface SideBarProps {
  navItems: NavItem[];
}

const SideBar: React.FC<SideBarProps> = ({ navItems }) => {
  const [isOpen, setIsOpen] = useState(false);
  const icons = [faHome, faInfoCircle, faCog, faUser, faServer, faCloud];

  const toggleSidebar = () => {
    setIsOpen(!isOpen);
  };

  return (
    <>
      <button
        onClick={toggleSidebar}
        className="fixed top-5 left-5 z-50 md:hidden"
      >
        <FontAwesomeIcon icon={faBars} className="text-primary" />
      </button>
      <div
        className={`fixed top-0 left-0 h-screen bg-primary text-sizeColor transition-transform duration-300 ${
          isOpen ? "translate-x-0" : "-translate-x-full"
        } md:translate-x-0 md:w-56`}
      >
        <button
          onClick={toggleSidebar}
          className="absolute top-5 right-5 z-50 md:hidden"
        >
          <FontAwesomeIcon icon={faTimes} className="text-backgroundColor" />
        </button>
        <div className="flex flex-col items-center justify-between h-full w-56">
          <div className="flex flex-col items-center space-y-4 mt-5 flex-grow">
            {navItems.map((item, index) => (
              <a
                key={index}
                href={item.href}
                className="text-center text-secondary hover:text-sizeColor font-poppins text-sm pt-3 flex items-center space-x-2 w-full"
              >
                <FontAwesomeIcon icon={icons[index]} className="mr-2" />
                <span className="flex-grow text-left">{item.name}</span>
              </a>
            ))}
          </div>
          <p className="text-center text-secondary font-poppins text-sm mb-5">
            Created by CodeRequest
          </p>
        </div>
      </div>
    </>
  );
};

export default SideBar;
